import React from "react";
import { Link } from "react-scroll";
import "./NavBar.css";

function NavBar() {
  return (
    <div className="d-flex flex-rows">
      <Link to="home" className="mx-4 nav-items" smooth={true}>
        Home
      </Link>
      <Link to="aboutme" className="mx-4 nav-items" smooth={true}>
        About Me
      </Link>
      <Link to="resume" className="mx-4 nav-items" smooth={true}>
        Resume
      </Link>
      <Link to="contactme" className="mx-4 nav-items" smooth={true}>
        Contact Me
      </Link>
    </div>
  );
}

export default NavBar;
