import React from "react";
import "./AboutMe.css";

function AboutMe() {
  return (
    <div className="about-me-container my-5" id="aboutme">
      <div className="about-me-parent">
        <span className="section-text-title">About Me</span>
        <span className="section-text-why">Why choose me?</span>
        <div className="about-me-card row shadow-lg my-5">
          <div className="col-lg-6 col-md-6 col-sm-12 d-flex align-items-center justify-content-center about-me-lottie">
            <lottie-player
              src="https://assets1.lottiefiles.com/packages/lf20_k86wxpgr.json"
              background="transparent"
              speed="1"
              loop
              autoplay
            ></lottie-player>
          </div>
          <div className="col-lg-6 col-md-6 col-sm-12 d-flex flex-column py-5">
            <span className="about-me-some-text">
              Front-end web developer with basic knowledge of the backend, as
              well as the ability to create applications with maximum
              efficiency. Responsible, attentive, logical thinking specialist,
              ready to become a valuable asset for the organization
            </span>
            <span className="few-highlights">
              <span className="few-text">Here are few highlights</span>
              <span className="few-list">
                <ul>
                  <li>Interactive Front-end as per the design</li>
                  <li>Layout of HTML pages according to the layout</li>
                  <li>Interaction with PostgreSQL using Sequelize</li>
                  <li>Backend stack Node.js and Express.js</li>
                  <li>Writing scripts in Python</li>
                </ul>
              </span>
            </span>
          </div>
        </div>
      </div>
    </div>
  );
}

export default AboutMe;
