import React, { useContext } from "react";
import "./Header.css";
import NavBar from "./NavBar/NavBar";
import OpenNav from "../../Context/Context";

function Header() {
  const { openNavBar, setOpenNavBar } = useContext(OpenNav);
  return (
    <div className="navbar-option">
      <div className="header-container  px-9 py-5">
        
        <nav
          className={
            !openNavBar
              ? "d-flex flex-rows navbar-new transition-all"
              : "d-flex flex-rows navbar-new-active transition-all"
          }
        >
          <span className="brand-name">Kirill Reutov</span>
          <NavBar />
        </nav>
      </div>
      <div className="navbar-option-mobile">
        <div className="d-flex flex-row justify-content-between ">
          <span className="brand-name d-flex align-self-center">Kirill Reutov</span>
          <div
            className="bars"
            onClick={() => setOpenNavBar(!openNavBar)}
          ></div>
        </div>
      </div>
    </div>
  );
}

export default Header;
