import React, { useState, useRef } from "react";
import "./contactme.css";
import emailjs from "@emailjs/browser";
import { Alert, AlertTitle, LinearProgress } from "@mui/material";

function ContactMe() {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [message, setMessage] = useState("");
  const [alertSuccess, setAlertSuccess] = useState(false);
  const [alertError, setAlertError] = useState(false);
  const [progress, setProgress] = useState(false);

  const form = useRef();

  const sendEmail = (e) => {
    setProgress(true);

    e.preventDefault();

    emailjs
      .sendForm(
        "service_d02n7xd",
        "template_1iighoi",
        form.current,
        "NiF2d_NR95e2hAVr3"
      )
      .then(
        (result) => {
          console.log(result.text);
          setAlertSuccess(true);
          setProgress(false);
          closeAlert(setAlertSuccess, false);
        },
        (error) => {
          console.log(error.text);
          setAlertError(true);
          setProgress(false);
          closeAlert(setAlertError, false);
        }
      );
  };

  function closeAlert(func, value) {
    setTimeout(() => {
      func(value);
    }, 5000);
  }

  return (
    <div className="contact-me-container">
      <div className="relative-bg"></div>
      <div className="contact-me-parent d-flex flex-column" id="contactme">
        <span className="section-text-title">Contact Me</span>
        <span className="section-text-why">Lets keep in Touch</span>

        <div className="contact-me-card row">
          <div className="col-lg-6 col-md-5 col-sm-12 px-2 py-2 left-contact">
            <span className="get-in-touch mx-4 my-5">Get In Touch</span>
            <div className="py-4 d-flex justify-content-center contact-me-lottie">
              <lottie-player
                src="https://assets10.lottiefiles.com/packages/lf20_3ktmthuy.json"
                background="transparent"
                speed="1"
                style={{ width: "350px", height: "350px" }}
                loop
                autoplay
              ></lottie-player>
            </div>
          </div>
          <div className="col-lg-6 col-md-5 col-sm-12 my-auto">
            <form
              ref={form}
              onSubmit={sendEmail}
              className="d-flex flex-column right-contact-card"
            >
              <div className="input-group my-3 d-flex flex-column">
                <label>Name</label>
                <input
                  required
                  value={name}
                  onChange={(e) => {
                    setName(e.target.value);
                  }}
                  type="text"
                  placeholder="Enter Your Name"
                  className="input-groups"
                  name="user_name"
                />
              </div>
              <div className="input-group my-3 d-flex flex-column">
                <label>Email</label>
                <input
                  required
                  value={email}
                  onChange={(e) => {
                    setEmail(e.target.value);
                  }}
                  type="email"
                  placeholder="Enter Your Email"
                  className="input-groups"
                  name="user_email"
                />
              </div>
              <div className="input-group my-3 d-flex flex-column">
                <label>Message</label>
                <textarea
                  required
                  value={message}
                  onChange={(e) => {
                    setMessage(e.target.value);
                  }}
                  type="text"
                  placeholder="Enter Your Message"
                  className="input-groups"
                  name="message"
                />
              </div>
              <div className="input-group my-3 d-flex flex-column">
                <input
                  className="btn btn-success"
                  type="submit"
                  value="Send Message"
                />
                {progress === true && (
                  <LinearProgress className="linear-settings" />
                )}
                {alertSuccess === true && (
                  <Alert
                    severity="success"
                    className="alert-settings"
                    onClose={() => {
                      setAlertSuccess(!alertSuccess);
                    }}
                  >
                    <AlertTitle>Success</AlertTitle>
                    Your message has been sent!
                  </Alert>
                )}
                {alertError === true && (
                  <Alert
                    severity="error"
                    className="alert-settings"
                    onClose={() => {
                      setAlertError(!alertError);
                    }}
                  >
                    <AlertTitle>Error</AlertTitle>
                    Looks like something went wrong...
                  </Alert>
                )}
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
}

export default ContactMe;
