import React from "react";
import "./Programming.css";

function Programming() {
  const value = [
    {
      text: "JavaScript",
      percentAge: 60,
    },
    {
      text: "HTML",
      percentAge: 80,
    },
    {
      text: "CSS/SASS",
      percentAge: 80,
    },
    {
      text: "React JS",
      percentAge: 50,
    },
    {
      text: "Python",
      percentAge: 55,
    },
    {
      text: "Node JS",
      percentAge: 40,
    },
    {
      text: "Express JS",
      percentAge: 35,
    },
    {
      text: "PostgreSQL",
      percentAge: 50,
    },
  ];
  return  (
  <div className="programming-container">
        <div className="row">
            {value.map((value,index)=>{
                return (
                <div className="col-lg-6 col-md-6 col-sm-12 py-1" key={index}>
                    <span className="language" >{value.text}</span>
                    <div className="progress-bar">
                        <div className="progress-new" style={{width:`${value.percentAge}%`}}></div>
                    </div>
                </div>
                )
            })}
        </div>
  </div>
  )
}

export default Programming;
