import React from "react";
import "./Profile.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faTelegram,
  faInstagram,
  faVk,
  faGithub,
} from "@fortawesome/free-brands-svg-icons";
import fontawesome from "@fortawesome/fontawesome";
import Typed from "react-typed";
import { Link} from "react-scroll";

export default function Profile() {
  fontawesome.library.add(faTelegram, faInstagram, faVk, faGithub);
  return (
    <div className="profile-container">
      <div className="profile-parent">
        <div className="profile-details">
          <div className="colz">
            <div className="colz-icon">
              <a href="http://instagram.com/toxicreik">
                <FontAwesomeIcon icon="fab fa-instagram" />
              </a>
              <a href="https://vk.com/toxicreik">
                <FontAwesomeIcon icon="fab fa-vk" />
              </a>
              <a href="https://t.me/realreik">
                <FontAwesomeIcon icon="fab fa-telegram" />
              </a>
              <a href="https://github.com/iamreik">
                <FontAwesomeIcon icon="fab fa-github" />
              </a>
            </div>
          </div>

          <div className="profile-details-name">
            <span className="primary-text">
              {" "}
              Hello, I'M <span className="highlighted-text">Kirill Reutov</span>
            </span>
          </div>

          <div className="profile-details-role">
            <span className="primary-text">
              {" "}
              <h1>
                {" "}
                <Typed
                  strings={[
                    "Front-end Developer",
                    "Cross Platform Dev",
                    "React Developer",
                  ]}
                  typeSpeed={70}
                  backSpeed={70}
                  loop
                />
              </h1>
              <span className="profile-role-tagline">
                {" "}
                Knack of building applications with frontend operations
              </span>
            </span>
          </div>
          <div className="profile-options">
            <Link className="btn primary-btn" to="contactme" smooth={true}>
              {""}
              Hire Me{" "}
            </Link>

            <a
              href="ReutovKirillResume.pdf"
              download="Reutov Kirill Resume.pdf"
            >
              <button className="btn highlighted-btn">Get Resume</button>
            </a>
          </div>
        </div>
        <div className="profile-picture">
          <div className="profile-picture-background"></div>
        </div>
      </div>
    </div>
  );
}
