import React, { useState } from "react";
import "./Resume.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faBriefcase,
  faCode,
  faTasks,
  faGraduationCap,
} from "@fortawesome/fontawesome-free-solid";
import fontawesome from "@fortawesome/fontawesome";
import Education from "./Education/Education";
import Work from "./Work/Work";
import Programming from "./Programming/Programming";
import Projects from "./Projects/Projects";

function Resume() {
  fontawesome.library.add(faCode, faBriefcase, faTasks, faGraduationCap);
  const [education, setEducation] = useState(true);
  const [workHistory, setWorkHistory] = useState(false);
  const [programmings, setProgrammings] = useState(false);
  const [projects, setProjects] = useState(false);

  function SetAllFalse() {
    setEducation(false);
    setWorkHistory(false);
    setProgrammings(false);
    setProjects(false);
  }

  function ChangeState(function1, state1) {
    SetAllFalse();
    function1(state1);
  }
  return (
    <div className="resume-container py-5" id="resume">
      <div className="resume-parent">
        <span className="section-text-title">Resume</span>
        <span className="section-text-why">My formal Bio Details</span>

        <div
          className="resume-content row"
          style={{ width: "70%", marginInline: "auto", minHeight: "456px" }}
        >
          <div className="col-lg-4  col-md-4 col-sm-12 resume-left-section px-0 shadow-lg">
            <div className="d-flex flex-column bg-new text-white">
              <span className="icons-span">
                <FontAwesomeIcon icon="graduation-cap" />{" "}
              </span>
              <span className="icons-span">
                <FontAwesomeIcon icon="briefcase" />{" "}
              </span>
              <span className="icons-span">
                <FontAwesomeIcon icon="code" />{" "}
              </span>
              <span className="icons-span">
                <FontAwesomeIcon icon="tasks" />{" "}
              </span>
            </div>
            <div className="d-flex flex-column ">
              <span
                className={
                  education === false
                    ? "resume-options-items"
                    : " selected resume-options-items"
                }
                onClick={() => {
                  ChangeState(setEducation, true);
                }}
              >
                Education
              </span>
              <span
                className={
                  workHistory === false
                    ? "resume-options-items"
                    : " selected resume-options-items"
                }
                onClick={() => {
                  ChangeState(setWorkHistory, true);
                }}
              >
                Work
              </span>
              <span
                className={
                  programmings === false
                    ? "resume-options-items"
                    : " selected resume-options-items"
                }
                onClick={() => {
                  ChangeState(setProgrammings, true);
                }}
              >                 Programming
              </span>
              <span
                className={
                  projects === false
                    ? "resume-options-items"
                    : " selected resume-options-items"
                }
                onClick={() => {
                  ChangeState(setProjects, true);
                }}
              >
                Projects
              </span>
            </div>
          </div>

          <div className="col-lg-8  col-md-8 resume-right-section">
            {education === true && <Education />}
            {workHistory === true && <Work />}
            {programmings === true && <Programming />}
            {projects === true && <Projects />}
          </div>
        </div>
      </div>
    </div>
  );
}

export default Resume;
